console.time('boot-time');

import { $i, $q, $a, $x } from './lib/constants.js';
import utils from './lib/utils.js';
import actions from './state/actions.js';
import mutations from './state/mutations.js';
import EventManager from './state/EventManager.js'
import Store from './state/Store.js';

import BaseComponent from './components/BaseComponent.js';
import ExampleComponent from './components/ExampleComponent.js';

import App from './App.js'
import { Player } from './models/Player.js';

const app = new App({
    store: new Store({
        actions,
        mutations,
        events: new EventManager()
    }),
    components: [],
    $x: $x,
    init: (self) => {
        const overlay = $i('overlay');
        const navBtn = $i('menu-bars');
        const nav = $i('nav');
        const navs = [$i('nav-1'), $i('nav-2'), $i('nav-3'), $i('nav-4'), $i('nav-5'), $i('nav-6')];

        const toggleNav = () => {
            navBtn.classList.toggle('change');
            overlay.classList.toggle('overlay-active');

            if (overlay.classList.contains('overlay-active')) {
                navs.forEach((nav, i) => nav.classList.replace(`nav-${i + 1}-slide-out`, `nav-${i + 1}-slide-in`));
                overlay.classList.replace('overlay-slide-out', 'overlay-slide-in');
            } else {

                navs.forEach((nav, i) => nav.classList.replace(`nav-${i + 1}-slide-in`, `nav-${i + 1}-slide-out`));
                overlay.classList.replace('overlay-slide-in', 'overlay-slide-out');
            }
        }

        $i('menu-bars').addEventListener('click', toggleNav);
        $i('nav').addEventListener('click', toggleNav);
        overlay.hidden = false;

        self.registerComponent(new ExampleComponent(app.store));
        self.registerPlayer(new Player(), $x);
    },
});

window.onload = () => app.init(app);

console.timeEnd('boot-time');

utils.log('App:', app, '#bada55');