import { keys, $x } from '../lib/constants.js';

export class Player {
    constructor(store) {
        let _stash = new Proxy({}, {
            set: (obj, prop, value) => {
                const oldValue = obj[prop];
                obj[prop] = value;
                store.events.publish(keys.$eventKey.STASH_CHANGED, { prop, val, oldValue });
            }
        });

        let _trenchCoat = new Proxy([], {
            set: (obj, prop, val) => {
                const oldValue = obj[prop];
                obj[prop] = value;
                store.events.publish(keys.$eventKey.TRENCH_COAT_CHANGED, { prop, val, oldValue });
            }
        })

        this.stash = this.getStashMods(_stash);
        this.trenchCoat = this.getTrenchCoatMods(_trenchCoat);

        this.stash.set(2000, $x);
        this.trenchCoat.set(new Array(100), $x);
    }

    getStashMods(_stash) {
        return {
            inc: (val, k) => k == $x ? _stash += val : void 0,
            dec: (val, k) => k == $x ? _stash -= val : void 0,
            set: (val, k) => k == $x ? _stash = val : void 0,
            get: () => _stash
        }
    }

    getTrenchCoatMods(_trenchCoat) {
        return {
            add: (val, k) => k == $x ? _trenchCoat.push(val) : void 0,
            remove: (val, k) => k == $x ? _trenchCoat.splice(_trenchCoat.indexOf(val)) : void 0,
            set: (val, k) => k == $x ? _trenchCoat = val : void 0,
            getCoat: () => _trenchCoat
        }
    }
}

