import utils from './Utils.js';

const $i = element => document.getElementById(element);
const $q = (element) => document.querySelector(element);
const $a = (element) => document.querySelectorAll(element);

const $colors = {
    grey: '#696969',
    limegreen: '#bada55',
    lightblue: '#7fe5f0',
    brightred: '#ff0000',
    pink: '#ff80ed',
    seablue: '#407294',
    beige: '#cbcba9',
    white: '#ffffff',
    maroon: '#420420',
    grassgreen: '#065535',
    lightgrey: '#c0c0c0',
    yellow: '#ffd700',
    orange: '#ffa500',
    darkred: '#800000',
    limegreen: '#00ff00',

};

const $CRYPTO = {
    BTC: 'btc',
    DIVI: 'divi',
    NANO: 'nano',
    DOGE: 'doge',
    ETH: 'eth',
    RIPPLE: 'ripple',
};

let keys = {
    $eventKey: {
        STATE_CHANGE: 'stateChange',
        CRYPTO_BOUGHT: 'cryptoBought',
        STASH_CHANGED: 'stashChanged',
        TRENCH_COAT_CHANGED: 'trenchCoatChanged',
    },
    $actionKey: {},
    $mutationKey: {}
};

(() => {
    let keysToMap = [];
    Object.keys(keys).forEach($keyType => {
        Object
            .keys(keys[$keyType])
            .map(k => {
                if (keysToMap.includes({ [k]: keys[$keyType][k] })) return;
                return keysToMap.push(new Object({ [k]: keys[$keyType][k] }));
            });
    })

    keysToMap.forEach((el, i, arr) => {
        keys.$actionKey[Object.keys(el)[0]] = Object.values(el)[0];
        keys.$mutationKey[Object.keys(el)[0]] = Object.values(el)[0];
    });

    return void 0; 
})();

const $k = [];

(function gen(n) { 
    if (n > 0) {
        $k.push(utils.genRandomToken());
        return gen(--n);
    }

    return false
})(20);

const $x = (() => $k[utils.getRandomInt(0, Object.keys($k).length - 1)])();

utils.log('keys:', keys, $colors.orange);
utils.log('x-token:', $x, $colors.grassgreen);

export { $i, $q, $a, keys, $x, $CRYPTO, $colors };