class Utils {
    constructor() {
        this.log = this.createLog();
    }

    createLog() {
        let n = 0;
        return (msg, obj = {}, color = null) => {
            if (msg instanceof Error) {
                console.error(`${new Date().toISOString()}-LOG-#${++n} => ${msg}`, obj);
                return false;
            }
            console.log(`%c${new Date().toISOString()}-LOG-#${++n} => ${msg}`, `${color ? 'color:' + color : ''}`, obj);
            return true;
        }
    }

    deepEqual(obj1, obj2) {
        return JSON.stringify(obj1) === JSON.stringify(obj2);
    }

    deepClone() {
        
    }

    shallowClone() {

    }

    timeout(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    async wait(ms) {
        return await Promise.all([this.timeout(ms / 2)], [this.timeout(ms / 2)]);
    }

    getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
    }

    genRandomToken() {
        return (function gen(n, tArr){
            if (n === 5) return tArr;
            tArr.push(Math.random().toString(36).substring(2, 15));
            return gen(++n, tArr)
        })(0, []).join('');
    }
}

export default new Utils();